const VideoSection = ({
  currentVideo,
  open,
  onClickContent,
  introduction,
  nextContent,
  contentDone,
  progress,
  courseDataId,
  module,
}) => {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: playingAnimation,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };

  const [requested, setRequested] = useState(false);
  const [done, setDone] = useState(false);

  const success = () => {
    console.log("success");
    setDone(true);
  };

  const failed = () => {
    console.log("failed");
    setDone(false);
    setRequested(false);
  };

  return (
    <section style={{ width: "100%" }} className={styles.videoSection}>
      <div className={styles.videoTitle}>
        <h2>{currentVideo.title}</h2>
      </div>
      <div
        style={{ background: open ? "white" : "black" }}
        className={styles.videoContainer}
      >
        <Vimeo
          // video={
          //   currentVideo.content_info.source ||
          //   "https://player.vimeo.com/video/501387150"
          // }
          video={
            currentVideo.content_info
              ? currentVideo.content_info.source
              : "https://player.vimeo.com/video/501387150"
          }
          // autoplay
          height={open ? 500 : 600}
          muted={false}
          controls={true}
          onEnd={() => nextContent(currentVideo.slug)}
          onProgress={(videoProgress) => {
            console.log(progress);
            console.log("percentage", videoProgress.percent);
            console.log("requested", requested);
            console.log("done", done);
            console.log(
              "is_done",
              progress[module] ? progress[module][currentVideo.slug] : false
            );
            console.log(
              videoProgress.percent > 0.89 &&
                !requested &&
                !done &&
                !(progress[module]
                  ? progress[module][currentVideo.slug]
                  : false)
            );
            if (
              videoProgress.percent > 0.89 &&
              !requested &&
              !done &&
              !(progress[module] ? progress[module][currentVideo.slug] : false)
            ) {
              console.log("done");
              contentDone(
                courseDataId,
                module,
                currentVideo.slug,
                success,
                failed
              );
              setRequested(true);
            }
          }}
          onLoaded={() => {
            console.log("on readyyy");
            setRequested(false);
            setDone(false);
          }}
        />
      </div>
      <div
        style={{ width: open ? "100%" : "50%" }}
        className={styles.videoList}
      >
        {introduction.map((item, i) => {
          return (
            <div
              onClick={() => onClickContent(item.slug)}
              key={item.title + i}
              className={styles.listItemVideo}
            >
              <div className={styles.videoAction}>
                <img
                  src={
                    item.content_type === "v"
                      ? item === currentVideo
                        ? pauseIcon
                        : playIcon
                      : bulbIcon
                  }
                  alt="video-action"
                />
              </div>
              <div
                onClick={() => onClickContent(item.slug)}
                className={styles.videoDetail}
              >
                <h4>{item.title}</h4>
                {item === currentVideo ? (
                  <div className={styles.playingAnimation}>
                    <Lottie
                      options={defaultOptions}
                      height={40}
                      width={40}
                      isStopped={false}
                      isPaused={false}
                    />
                  </div>
                ) : progress[module] && progress[module][item.slug] ? (
                  <img src={checkIcon} alt="complete-robofreaks" />
                ) : (
                  ""
                )}
              </div>
            </div>
          );
        })}
      </div>
    </section>
  );
};

const CourseModules = ({
  modules,
  currentModule,
  robocoins,
  onClickModule,
}) => {
  return (
    <div className={styles.modulesContainer}>
      {modules.map((item, i) => {
        let active = item.slug === currentModule;
        let locked = item.metadata.robocoins_to_unlock > robocoins;
        return (
          <div
            key={item.title + "module" + i}
            className={classNames(
              styles.moduleCard,
              active ? styles.activeCard : "",
              locked ? styles.showLocked : ""
            )}
            onClick={() =>
              !active && !locked ? onClickModule(item.slug) : null
            }
          >
            <div className={styles.moduleIcon}>
              <img
                // src={item.icon}
                src={catIcon}
                alt="module-icon"
              />
            </div>
            <div className={styles.content}>
              <div className={styles.details}>
                <h4>{item.title}</h4>
                <p>{item.description}</p>
                <div className={styles.items}>
                  <div className={styles.mediaItem}>
                    <img
                      src={active ? penIconWhite : penIcon}
                      alt="course-items"
                    />
                    {item.metadata.num_of_assignments}
                  </div>
                  <div className={styles.mediaItem}>
                    <img
                      src={active ? videoIconWhite : videoIcon}
                      alt="course-items"
                    />
                    {item.metadata.num_of_videos}
                  </div>
                  <div className={styles.mediaItem}>
                    <img
                      src={active ? listIconWhite : listIcon}
                      alt="course-items"
                    />
                    {item.metadata.num_of_quizzes}
                  </div>
                </div>
              </div>
            </div>
            {locked
              ? null
              : item.metadata.robocoins_to_unlock && (
                  <div className={styles.coins}>
                    <p>{item.metadata.robocoins_to_unlock}</p>
                    <img src={coinsIcon} alt="icons" />
                  </div>
                )}
            {locked && (
              <>
                <div className={styles.locked}>
                  <img src={lockedIcon} alt="icons" />
                </div>
                <div className={styles.moduleLocked}>
                  <div className={styles.lockedHeading}>
                    <h5>This module is locked</h5>
                    <img src={lockedIcon} alt="locked-module" />
                  </div>
                  <div className={styles.coinsNeeded}>
                    <h3>
                      You need {item.metadata.robocoins_to_unlock} Robocoins{" "}
                      <span>
                        <img src={coinsIcon} alt="coins" />
                      </span>{" "}
                      to unlock this course
                    </h3>
                  </div>
                </div>
              </>
            )}
          </div>
        );
      })}
    </div>
  );
};

