function Course(props) {
  let { course, module } = useParams();

  const success = () => {
    console.log("success");
  };

  const failed = () => {
    props.history.push("/home");
  };

  useEffect(() => {
    props.fetchCourseData(course, success, failed);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    console.log("fetchingggggggggg");
    props.fetchModuleData(course, module, success, failed);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [module]);

  if (
    Object.keys(props.courses.courseData).length > 0 &&
    props.courses.courseData.constructor === Object &&
    Object.keys(props.courses.moduleData).length > 0 &&
    props.courses.moduleData.constructor === Object
  ) {
    return (
      <CourseVideo
        courseData={props.courses.courseData.course}
        courseDataId={props.courses.courseData.id}
        moduleData={props.courses.moduleData}
        robocoins={props.courses.courseData.additional_info.robocoins}
        progress={props.courses.courseData.additional_info.progress}
        history={props.history}
        contentDone={props.contentDone}
      />
    );
  } else {
    return (
      <div className="loader">
        <CustomLoader />
      </div>
    );
  }
}

export default withRouter(Course);
